@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Russo+One&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Russo One", sans-serif !important;
}

html {
  scroll-behavior: smooth;
}

input:focus {
  outline: none !important;
}

input::-ms-input-placeholder {
  /* Edge 12-18 */
  color: #556172 !important;
}

input::placeholder,
textarea::placeholder {
  color: #556172 !important;
}

.ant-input-outlined:focus,
.ant-input-outlined:focus-within {
  border: 0 !important;
  box-shadow: none !important;
  /* background: #262b3a !important; */
}
.ant-select-selector :focus,
.ant-select-selector :focus-within {
  border: 0 !important;
  box-shadow: none !important;
}

.ant-input-outlined,
.ant-select-selector {
  outline: none !important;
  box-shadow: none !important;
  border: 0 !important;
  border-radius: 10px !important;
}

.box-shadow-custom {
  box-shadow: 0px 0px 16.7px 0px #1e96c8;
}

a {
  cursor: pointer;
}

img {
  width: 100%;
}

body {
  margin: 0 !important;
  color: #fff;
  background: #000;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.ant-form-item-margin-offset {
  margin-bottom: 0 !important;
}

.ant-layout {
  background: #000;
  background-size: cover;
  background-attachment: fixed;
  background-position: bottom;
}

.ant-space-item {
  display: flex;
}

.ant-layout .ant-layout-sider,
.ant-menu-dark.menu-main,
.ant-menu-dark.menu-main > .ant-menu {
  background: #1d2633;
}

.ant-menu.menu-main .ant-menu-item {
  font-weight: 600;
  font-size: 15px;
}

.ant-menu-dark.menu-launchpad-list,
.ant-menu-dark.menu-launchpad-list
  > .ant-menu
  .ant-menu.menu-main
  .ant-menu-item:first-child {
  height: 90px !important;
  display: flex;
  align-items: center;
  margin: 0 !important;
}

.ant-layout-header {
  height: 90px;
  display: flex;
  align-items: center;
}

.ant-menu-inline-collapsed-tooltip {
  display: none;
}

.ant-btn {
  padding: 0;
  margin-left: -20px;
}

.ant-layout .ant-layout-header,
.ant-layout.ant-layout-footer {
  background: transparent !important;
}

a:hover {
  color: inherit;
}

.bg-primary-gradient {
  background: #0098ea;
}
.bg-secondary-gradient {
  background: #fd5065;
}
.text-primary-gradient {
  background: linear-gradient(90deg, #1e96c8 0%, #37aee2 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-secondary-gradient {
  background: linear-gradient(90deg, #9be1ff 0%, #1e96c8 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

#ton-connect-button button {
  width: 220px !important;
  height: 60px !important;
  border-radius: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
  font-size: 20px;
  text-transform: uppercase;
  background-image: url("./assets/img/bg-btn.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent !important;
  transition: all 0.3s linear;
}

.btn-primary {
  width: 220px !important;
  height: 60px !important;
  border-radius: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
  font-size: 20px;
  text-transform: uppercase;
  background-image: url("./assets/img/bg-btn.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent !important;
  transition: all 0.3s linear;
  cursor: pointer;
}

.btn-primary-full {
  cursor: pointer;
  max-width: 100%;
  width: 462px !important;
  height: 86px !important;
  margin: 0 auto;
  border-radius: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
  font-size: 20px;
  text-transform: uppercase;
  background-image: url("./assets/img/btn-primary-full.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent !important;
  transition: all 0.3s linear;
}

#ton-connect-button button div {
  color: #000 !important;
  font-size: 20px;
}

#ton-connect-button button svg {
  display: none;
}

#ton-connect-button button:hover,
.btn-primary:hover,
.btn-primary-full:hover {
  transform: scale(1) !important;
  opacity: 0.8;
  color: #000;
}

.ant-layout-content {
  background-image: url("./assets/img/bg.png");
  background-size: cover;
  background-position: center;
}

button:disabled,
button[disabled] {
  cursor: not-allowed;
}

@media screen and (max-width: 768px) {
  .btn-primary-full {
    width: 200px !important;
    height: 55px !important;
    font-size: 16px;

    background-image: url("./assets/img/bg-btn.png");
  }
  .btn-primary {
    font-size: 16px;
  }

  .btn-primary-custom {
    width: 150px !important;
    height: 44px !important;
  }
}


.ant-table-cell, .ant-table-cell-row-hover, th.ant-table-cell{
  background-color: #171E21 !important;
  color: #ffffff !important;
  font-weight: normal !important;
  border-bottom: 1px solid #000000 !important;
}
.ant-table-cell::before, .ant-table-cell-row-hover::before, th.ant-table-cell::before{
  background-color: #171E21 !important
}

.ant-table-cell{
  text-transform: none;
}

.ant-pagination-item{
  color: #fff !important;
  background-color: #171E21 !important;
  font-weight: normal !important;
  border-color: transparent !important;
}
.ant-pagination-item a{
  color: #fff !important;
  
}

.ant-table{
  border-radius: 20px !important;
}
.wallet-list {
  height: 250px;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.wallet-list::-webkit-scrollbar {
  display: none;
}